import React, { useState } from 'react'
import Button from './Button'
import { useMutation } from '@apollo/client'
import { OPEN_VOTE, CLOSE_VOTE, GET_VOTE_STATE } from '../queries/queries'

const Cookies = require('js-cookie')

const AdminView = ({ voteState }) => {

    const [password, setPassword] = useState('')
    const [error, setError] = useState('')
    const [success, setSuccess] = useState('')
    const [voteIsOpen, setVoteIsOpen] = useState(voteState.voteOpen)

    const [openVote] = useMutation(OPEN_VOTE, {
        refetchQueries: [{ query: GET_VOTE_STATE }]
    })
    const [closeVote] = useMutation(CLOSE_VOTE, {
        refetchQueries: [{ query: GET_VOTE_STATE }]
    })

    setTimeout(() => {
        setError('')
        setSuccess('')
    }, 3000)

    const login = () => {
        if (password === process.env.REACT_APP_ADMIN_PASSWORD) {
            setError('')
            Cookies.set('admin_token', process.env.REACT_APP_ADMIN_TOKEN)
            setPassword('')
            setSuccess('Access granted yo.')
        } else {
            setSuccess('')
            setError('Väärä salasana.')
            setPassword('')
        }
    }

    const openVoting = async () => {
        try {
            await openVote()
            setError('')
            setVoteIsOpen(true)
            setSuccess('Äänestys avattu.')
        } catch (error) {
            setSuccess('')
            setError('Äänestystä ei voitu avata.')
        }
    }

    const closeVoting = async () => {
        try {
            await closeVote()
            setError('')
            setVoteIsOpen(false)
            setSuccess('Äänestys suljettu.')
        } catch (error) {
            setSuccess('')
            setError('Äänestystä ei voitu sulkea.')
        }
    }

    if (Cookies.get('admin_token') === process.env.REACT_APP_ADMIN_TOKEN) {
        return (
            <div className='Container'>
                <h3 className='Heading'>Admin</h3>
                {error !== '' && <p className='Error'>{error}</p>}
                {success !== '' && <p className='Success'>{success}</p>}
                {voteIsOpen === false && <Button type='Button-aanestys' text='Avaa äänestys' handleClick={() => openVoting()} />}
                {voteIsOpen === true && <p>Äänestys on auki.</p>}
                {voteIsOpen === true && <Button type='Button-aanestys' text='Sulje äänestys' handleClick={() => closeVoting()} />}
            </div>
        )
    }
    return (
        <div className='Container'>
            {error !== '' && <p className='Error'>{error}</p>}
            {success !== '' && <p className='Success'>{success}</p>}
            <input className='Aihe-input' value={password} type='password' onChange={(event) => setPassword(event.target.value)} />
            <Button type='Add-button' text='Login' handleClick={() => login()} />
        </div>
    )
}

export default AdminView
