import React, { useState } from 'react'
import './App.css'
import { useQuery } from '@apollo/client'
import TopicSelection from './components/TopicSelection'
import Navbar from './components/Navbar'
import OldTopics from './components/OldTopics'
import loadingAnimation from './resources/loading.gif'
import AddTopic from './components/AddTopic'
import AdminView from './components/AdminView'
import Main from './components/Main'
import { GET_VOTE_STATE } from './queries/queries'

const App = () => {

  const [body, setBody] = useState('main')
  const voteState = useQuery(GET_VOTE_STATE)

  if (voteState.loading) {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
        Loading...
      </div>
    )
  }

  if (body === 'main') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <Main />
      </div>
    )
  }

  if (body === 'topicSelection') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <TopicSelection voteState={voteState.data.getVoteState} setBody={setBody} />
      </div>
    )
  }

  //if (body === 'covered') {
  //  return (
  //    <div className='App'>
  //      <Navbar setBody={setBody} />
  //      <OldTopics />
  //    </div>
  //  )
  //}

  if (body === 'addTopic') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AddTopic />
      </div>
    )
  }

  if (body === 'adminLogin') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AdminView voteState={voteState.data.getVoteState} />
      </div>
    )
  }

}

export default App
