import { gql } from '@apollo/client'

export const GET_SPLITTED_LIST = gql`
query {
  getSplittedList  {
    leftList {
      topics {
        id
        name
      }
    }
    rightList {
      topics {
        id
        name
      }
    }
  }
}
`

export const ADD_VOTE = gql`
mutation addVote($vote: Int!) {
  addVote(vote: $vote) {
      round
      votes
      voteOpen
  }
}
`

export const GET_COVERED = gql`
query {
  getCovered  {
    id
    name
  }
}
`

export const CREATE_TOPIC = gql`
mutation createTopic($name: String!) {
  addTopic(
    name: $name,
  ) {
    id
    name
  }
}
`

export const OPEN_VOTE = gql`
mutation {
  openVote {
    round
    votes
    voteOpen
  }
}
`

export const CLOSE_VOTE = gql`
mutation {
  closeVote {
    round
    votes
    voteOpen
  }
}
`

export const GET_VOTE_STATE = gql`
query {
  getVoteState {
    round
    votes
    voteOpen
  }
}
`
